/* GameButton.css */
.game-button {
  padding: 1rem;
  font-size: 1.5rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  background: linear-gradient(45deg, #ffcc00, #00e5ff);
  color: #333;
  transition: background 0.3s ease;
}

.game-button:hover {
  background: linear-gradient(45deg, #ffaa00, #00e5ff);
}
