/* src/games/Peoples/Peoples.css */

.peoples-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 100vw;
  overflow: hidden; /* Prevent scrolling within the container */
  position: fixed;  /* Fix the container in place */
  top: 0;
  left: 0;
}

.peoples-title {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 2rem;
  color: #fff;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.7);
}

.peoples-game-area {
  position: relative;
  width: 100%;
  max-width: 50rem;
  height: 50rem;
  border: 0.2rem solid #444;
  background-color: rgba(255, 255, 255, 0.8);
  overflow: hidden;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}
