/* src/components/HoneycombAnimation.css */

.honeycomb-animation {
  position: absolute; /* Make position absolute to appear relative to button */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  z-index: 1000;
}

.honeycomb-cell {
  width: 60px;
  height: 60px;
  background-color: #ffcc00;
  clip-path: polygon(
    50% 0%,
    100% 25%,
    100% 75%,
    50% 100%,
    0% 75%,
    0% 25%
  );
  opacity: 0;
  position: absolute;
  pointer-events: none; /* Default: not clickable */
}

.honeycomb-cell-1 {
  background-image: url('../../../public/Swarm_Intelligence.png');
  background-size: cover;
  background-position: center;
  animation: appear-and-move-1 1s forwards;
}

.honeycomb-cell-2 {
  background-image: url('../../../public/Peoples.png');
  background-size: cover;
  background-position: center;
  animation: appear-and-move-2 1s forwards;
  pointer-events: all; /* Allow clicking */
  cursor: pointer;
  transition: background-color 0.3s;
}

.honeycomb-cell-2:hover {
  background-color: #ffaa00; /* Hover effect for feedback */
}

.honeycomb-cell-3 {
  background-image: url('../../../public/Ruler.png');
  background-size: cover;
  background-position: center;
  animation: appear-and-move-3 1s forwards;
}

.hovered-cell-name {
  position: fixed; /* Use fixed to ensure it aligns with viewport */
  transform: translate(-50%, -100%);
  background-color: rgba(255, 255, 255, 0.9);
  color: #333;
  font-size: 1rem;
  padding: 0.5rem;
  border-radius: 0.25rem;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  pointer-events: none; /* Prevent interaction with the tooltip */
  z-index: 10;
}

@keyframes appear-and-move-1 {
  0% {
    opacity: 0;
    transform: translate(0, 0) scale(0.5);
    pointer-events: none;
  }
  100% {
    opacity: 1;
    transform: translate(-80px, -150px) scale(1);
    pointer-events: all;
  }
}

@keyframes appear-and-move-2 {
  0% {
    opacity: 0;
    transform: translate(0, 0) scale(0.5);
    pointer-events: none; /* Disable pointer events during the animation */
  }
  100% {
    opacity: 1;
    transform: translate(0px, -180px) scale(1);
    pointer-events: all; /* Enable pointer events when the animation completes */
  }
}

@keyframes appear-and-move-3 {
  0% {
    opacity: 0;
    transform: translate(0, 0) scale(0.5);
    pointer-events: none;
  }
  100% {
    opacity: 1;
    transform: translate(80px, -150px) scale(1);
    pointer-events: all;
  }
}
