/* src/layouts/Honeycomb2Layout.css */

.honeycomb2-layout {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 100vw;
  background-image: url('../../public/Peoples.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 2rem;
  overflow: hidden; /* Prevent scrolling within the layout */
  position: fixed; /* Fix the layout in place */
  top: 0;
  left: 0;
}
